.title {
	@include f(20px, 500);
	color: $indigo;
	@include sm {
		font-size: 16px; }
	@include xs {
		font-size: 11px; } }
.title-main {
	@include f(50px, 500, 1); }
.title-l {
	@include f(24px, 500, 1); }
.title-mid {
	@include f(18px, 500); }
.title-s {
	@include f(16px, 500, 1); }
.title-sm {
	@include f(14px, 500);
	transition: color .3s;
	color: $indigo;
	text-transform: uppercase;
	@include hover {
		color: $accent; } }
