					right: 126px;
					top: 20px; } } } }
	&__link {
		display: inline-block;
		@include hover {
			.features__border {
				border-color: $accent; }
			.features__title {
				color: $accent; } }
		@include sm {
			display: flex;
			justify-content: flex-start;
			align-items: center; } }
	&__border {
		margin: 0 auto;
		@include circle(140);
		border: 2px solid $border;
		position: relative;
		transition: border-color .3s;
		@include sm {
			border: none; }
		&:after {
			content: '';
			position: absolute;
			display: block;
			width: 65px;
			height: 2px;
			background-color: $border;
			@include sm {
				display: none; } }
		@include sm {
			margin: 0; } }
	&__wrap {
		@include vcenter;
		@include circle(130);
		overflow: hidden;
		@include sm {
			height: auto;
			width: 100%;
			border: 4px solid transparent;
			box-shadow: 0 0 2px 2px $border; } }
	&__img {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		height: 130px;
		@include sm {
			height: auto;
			padding-bottom: 100%;
			width: 100%; } }
	&__title {
		max-width: 250px;
		margin-top: 5px;
		@include sm {
			margin: 0;
			margin-left: 20px;
			min-width: 175px; } }
	&__title-block {
		display: block;
		@include ms {
			font-size: 10px; } } }
