.nav {
	&__list {
		display: flex;
		@include md {
			flex-direction: column; } }
	&__item {
		&:not(:last-child) {
			margin-right: 40px;
			@include md {
				margin-right: 0;
				margin-bottom: 30px; } } }
	&__link {
		font-weight: 500;
		text-transform: uppercase;
		@include tr(color 200ms);
		@include hover {
			color: $accent; } } }
