.login_popup {
	background-color: $login-bg;
	visibility: hidden;
	opacity: 0;
	transition: opacity .3s, visibility .3s;
	@include sm {
		align-items: center; }
	&__container {
		position: relative;
		padding: 30px;
		@include sm {
			padding: 30px; } }
	&.is-open {
		visibility: visible;
		opacity: 1;
		align-items: center; } }
