.popup {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: rgba(235, 238, 240, 0.9);
	z-index: 5;
	min-width: 320px;
	overflow: auto;
	@include sm {
		bottom: auto;
		height: 100vh;
		align-items: flex-start; }
	&__container {
		max-width: 1100px;
		width: 100%;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 100px 40px;
		@include sm {
			height: auto;
			align-items: flex-start;
			padding: 100px 40px; } }
	&__wrap {
		@include sm {
			width: 100%;
			height: auto;
			position: static; }
		@include pseudo;
		&:before {
			background-image: url('../img/left-line.svg');
			// background-size: 100%
			background-repeat: no-repeat;
			width: 45px;
			height: 278px;
			top: 65px;
			pointer-events: none;
			@include sm {
				display: none; } }
		&:after {
			background-image: url('../img/right-line.svg');
			// background-size: 100%
			background-repeat: no-repeat;
			width: 45px;
			height: 278px;
			left: 510px;
			top: 65px;
			pointer-events: none;
			@include sm {
				display: none; } }
		[data-length-features="5"] &, [data-length-features="4"] &, [data-length-features="3"] & {
			&:before {
				background-image: url('../img/small-left-line.svg');
				background-repeat: no-repeat;
				width: 15px;
				height: 160px;
				top: 115px;
				pointer-events: none; } }
		[data-length-features="4"] & {
			&:after {
				background-image: url('../img/small-right-line.svg');
				background-repeat: no-repeat;
				width: 15px;
				height: 160px;
				left: 530px;
				top: 115px;
				@include rotate(-3);
				pointer-events: none; } }
		[data-length-features="3"] &, [data-length-features="2"] &, [data-length-features="1"] & {
			&:after {
				display: none; } }
		[data-length-features="2"] &, [data-length-features="1"] & {
			&:before {
				display: none; } } }
	&__description {
		display: none;
		@include sm {
			display: block; } }
	&__features {
		@include sm {
			margin-top: 40px; }
		@include ms {
			margin: 40px -25px 0; } } }

[data-modal] {
	opacity: 0;
	visibility: hidden;
	&.is-open {
		visibility: visible;
		opacity: 1; } }

