  opacity: 1;
}

.login_popup {
  background-color: rgba(112, 133, 149, 0.7);
  visibility: hidden;
  opacity: 0;
  transition: opacity .3s, visibility .3s;
}

@media only screen and (max-width: 784px) {
  .login_popup {
    align-items: center;
  }
}

.login_popup__container {
  position: relative;
  padding: 30px;
}

@media only screen and (max-width: 784px) {
  .login_popup__container {
    padding: 30px;
  }
}

.login_popup.is-open {
  visibility: visible;
  opacity: 1;
  align-items: center;
}

.login-form {
  padding: 45px 40px 40px;
  background-color: #fff;
  border-radius: 30px;
  max-width: 420px;
  position: relative;
  color: #405b71;
}

.login-form__title {
  padding-bottom: 5px;
}

.login-form__input-wrap {
  margin-top: 23px;
  display: inline-block;
  width: 100%;
}

.login-form__input {
  width: 100%;
  padding: 20px 20px 20px 26px;
  background-color: #e7ecf1;
  border-radius: 24px;
  font-size: 14px;
  font-family: "GothamPro", "Helvetica", sans-serif;
  color: #4e6e8a;
}

.login-form__input::placeholder {
  font-size: 14px;
  font-family: "GothamPro", "Helvetica", sans-serif;
  color: #4e6e8a;
}

.login-form__input:focus::placeholder {
  font-size: 0;
}

.login-form__message {
  color: #d78920;
  font-size: 11px;
  font-weight: 400;
  transition: max-height .3s, opacity .3s;
  max-height: 0;
  opacity: 0;
}

.login-form__button {
  margin-top: 30px;
}

.login-form__close {
  right: 12px;
  top: 16px;
}

.destination {
  color: #405b71;
}

.destination__bg {
  background-image: url("../img/destination.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 454px;
  box-shadow: inset 0 140px 475px 0px #fff;
}

.destination__container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.destination__wrap {
  margin: 115px 0 0 75px;
}

.destination__title {
  padding: 20px 0;
}

.breadcrumbs {
  display: flex;
}

.breadcrumbs__item {
  font-size: 12px;
  font-weight: 500;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 20px;
}

.breadcrumbs__item a {
  border-bottom: 1px solid #4e6e8a;
  color: #405b71;
  position: relative;
}

.breadcrumbs__item a:after {
  content: '';
  height: 4px;
  width: 4px;
  border-radius: 50%;
  background-color: #a1bdd3;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  left: calc(100% + 10px);
}

.breadcrumbs__item span {
  opacity: .8;
}

.article {
  position: relative;
  top: -118px;
}

.article__container {
  max-width: 1192px;
  position: relative;
}

.article__button {
  bottom: -32px;
  left: 50%;
  transform: translateX(-50%);
}

.drop {
  background-color: #d0dae3;
  border-top: 4px solid #d78920;
  padding: 34px 117px 60px;
  overflow: hidden;
}

.drop__title {
  text-align: center;
  color: #405b71;
}

.drop__text {
  margin-top: 23px;
}

.drop__text p {
  line-height: 22px;
}

.drop__text_hidden {
  display: none;
}

.integration-slider {
  margin-top: -35px;
}

.integration-slider__container {
  max-width: 1192px;
}

.integration-slider__title {
  color: #405b71;
  padding-bottom: 40px;
}

.integration-slider__button {
  text-align: center;
  margin-top: 40px;
}

.no-touch .integration-slider__button button:hover {
  color: #fff;
  background-color: #4e6e8a;
}

.slider-small {
  text-align: center;
}

.slider-small__list {
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
}

.slider-small__list.slick-initialized, .slider-small__list.is-active {
  opacity: 1;
  visibility: visible;
  overflow: visible;
  white-space: normal;
}

.slider-small__list.is-active {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.slider-small__item {
  display: inline-block;
}

.slider-small__list.is-active .slider-small__item {
  width: calc(25% - 53px);
  margin-bottom: 55px;
}

.slider-small__list.is-active .slider-small__item:not(:nth-child(4)):not(:last-child) {
  margin-right: 70px;
}

.no-touch .slider-small__link:hover .slider-small__title {
  color: #d78920;
}

.slider-small__list.is-active .slider-small__link {
  display: inline-block;
}

.slider-small__img-wrap {
  height: 232px;
  width: 232px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
}

.slider-small__img {
  height: 235px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.slider-small__title {
  margin-top: 35px;
  transition: color .3s;
}

.slider-small .slick-arrow {
  background-color: transparent;
  position: absolute;
  z-index: 2;
  top: 115px;
}

.no-touch .slider-small .slick-arrow:hover span {
  background-color: #d78920;
}

.slider-small .slick-arrow:before {
  position: absolute;
  content: '';
  top: -15px;
  bottom: -15px;
  left: 0;
  right: 0;
}

.slider-small .slick-arrow span {
  display: inline-block;
  width: 60px;
  height: 1px;
  background-color: #4e6e8a;
  position: relative;
  top: -4px;
  transition: background-color .3s;
}

.slider-small .slick-arrow span:before {
  content: '';
  position: absolute;
  top: -4.5px;
  left: -8px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4.5px 9px 4.5px 0;
  border-color: transparent #4e6e8a transparent transparent;
  transition: border-color .3s;
}

.slider-small .slick-prev {
  left: 0;
}

.no-touch .slider-small .slick-prev:hover span:before {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4.5px 9px 4.5px 0;
  border-color: transparent #d78920 transparent transparent;
}

.slider-small .slick-next {
  right: 0;
}

.no-touch .slider-small .slick-next:hover span:before {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4.5px 0 4.5px 9px;
  border-color: transparent transparent transparent #d78920;
}

.slider-small .slick-next span:before {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4.5px 0 4.5px 9px;
  border-color: transparent transparent transparent #4e6e8a;
  left: 60px;
}
