.specialization {
	text-align: center;
	@include sm {
		width: 50%;
		margin-bottom: 50px;
		padding: 0 15px; }
	&__link {
		display: inline-block;
		@include sm {
			display: block; } }
	&__img-border {
		@include circle(390);
		margin: 0 auto;
		@include pseudo;
		&:before {
			background-image: url('../img/item-hover-sm.svg');
			background-size: 100%;
			background-repeat: no-repeat;
			width: 350px;
			height: 150px;
			top: 257px;
			@include rotate(3);
			pointer-events: none; }
		&:after {
			background-image: url('../img/item-hover-big.svg');
			background-size: contain;
			background-repeat: no-repeat;
			width: 350px;
			height: 450px;
			left: 60px;
			top: -27px;
			pointer-events: none; }
		@include sm {
			height: auto;
			width: 100%;
			padding-bottom: 100%; } }
	&__img-wrap {
		@include vcenter;
		@include circle(364);
		overflow: hidden;
		@include sm {
			width: 95%;
			height: 95%; } }
	&__img {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		height: 400px;
		@include sm {
			height: 100px;
			width: 100%;
			padding-bottom: 110%; } }
	&__title {
		margin-top: 30px;
		@include tr(color 300ms);
		text-transform: uppercase;
		@include sm {
			margin-top: 15px; } } }


// Slider modifier
.specialization_slide {
	transition: border-color .3s, transform .3s, filter .3s;
	@include hover {
		transform: scale(1.1);
		@include sm {
			transform: scale(1); }
		.specialization__img-border {
			border-color: transparent;
			@include sm {
				border-color: $indigo; }
			&:before, &:after {
				opacity: 1;
				@include sm {
					display: none; } } }
		.specialization__title {
			color: $accent; } }
	& .specialization__img-border {
		border: 2px solid $border;
		@include tr(border 300ms);
		&:before, &:after {
			opacity: 0;
			@include tr(opacity 0.3s); } } }
// Slider modifier


// Popup modifier
.specialization_popup {
	position: relative;
	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(64, 91, 113, 0.7);
		color: $white;
		font-size: 16px;
		@include sm {
			display: none; } }
	&__overlay-inner {
		@include vcenter;
		width: 300px; }
	&__overlay-button {
		margin-top: 30px;
		color: $white;
		border: 1px solid $white;
		@include hover {
			color: $indigo;
			background-color: $white; }
		@include sm {
			color: $indigo;
			border-color: $indigo; } }
	&__button {
		top: -35px;
		right: 130px;
		@include sm {
			top: -8px;
			right: -50px; } }
	& .specialization__link {
		padding: 0 75px;
		position: relative;
		@include sm {
			padding: 0; } }
	& .specialization__img-wrap {
		@include circle(437);
		@include sm {
			width: 100%;
			height: 100%;
			max-width: 400px;
			max-height: 400px; } }
	& .specialization__img-border {
		&:before {
			width: 400px;
			height: 165px;
			top: 280px;
			left: -28px;
			@include sm {
				width: 90%;
				height: 40%;
				top: 70%;
				left: 0%; } }
		&:after {
			width: 420px;
			height: 510px;
			top: -60px;
			left: 40px;
			@include sm {
				width: 90%;
				height: 110%;
				top: -4%;
				left: 17%; } } }
	& .specialization__img {
		height: 500px;
		@include sm {
			height: 100px;
			padding-bottom: 100%;
			width: 100%; } }
	& .specialization__title {
		color: $accent;
		margin-top: 90px;
		@include sm {
			color: $indigo-title;
			font-size: 16px;
			margin-top: 40px; } }
	@include sm {
		position: static;
		width: 100%;
		padding: 0 30px;
		max-width: 450px;
		max-height: 450px;
		margin: 0 auto 50px; } }

// Popup modifier
