.button {
	background-color: transparent;
	display: inline-block;
	color: $indigo;
	font-family: $gotham;
	cursor: pointer;
	&_border {
		padding: 7px 20px;
		border: 1px solid $indigo;
		border-radius: 14px;
		transition: color .3s, background-color .3s; }
	&_close {
		position: absolute;
		@include circle(63);
		background-color: $accent;
		background-image: url('../img/close.svg');
		background-repeat: no-repeat;
		background-position: center;
		cursor: pointer;
		@include sm {
			@include circle(52); }
		&_small {
			@include circle(43);
			background-image: url('../img/close-small.svg');
			background-repeat: no-repeat;
			background-position: center;
			@include sm {
				@include circle(43); } } }
	&_sm {
		padding: 20px 30px;
		font-size: 16px;
		color: $white;
		border-radius: 40px;
		background-color: $accent;
		min-width: 160px; }
	&_arrow {
		position: absolute;
		@include circle(63);
		background-color: $accent;
		background-image: url('../img/arr-down.svg');
		background-repeat: no-repeat;
		background-position: center;
		cursor: pointer;
		&.is-active {
			background-image: url('../img/arr-up.svg'); } } }
