.breadcrumbs {
	display: flex;
	&__item {
		@include f(12px, 500);
		&:not(:last-child) {
			margin-right: 20px; }
		& a {
			border-bottom: 1px solid $indigo;
			color: $indigo-title;
			position: relative;
			&:after {
				content: '';
				@include circle(4);
				background-color: #a1bdd3;
				@include vcenter;
				left: calc(100% + 10px); } }
		& span {
			opacity: .8; } } }
