.footer {
	padding-top: 20px;
	display: flex;
	align-items: center;
	@include sm {
		height: auto; }
	&__container {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		align-items: center;
		@include sm {
			flex-direction: column;
			align-items: flex-start; } }
	&__copy {
		padding: 0 18px 18px 0;
		@include lg {
			order: 2; } } }

.contacts {
	display: flex;
	flex-wrap: wrap;
	@include sm {
		flex-direction: column;
		margin-bottom: 20px; }
	&__item {
		padding-bottom: 20px;
		&:not(:last-child) {
			margin-right: 40px;
			@include sm {
				margin-right: 0; } }
		@include hover {
			.icon-map, .icon-phone, .icon-mail {
				fill: $accent; }
			.contacts__text {
				opacity: 0.7; } }
		@include sm {
			&:nth-child(1) {
				order: 3; }
			&:nth-child(2) {
				order: 2; }
			&:nth-child(3) {
				order: 1; } }
		@include xs {
			font-size: 13px; } }
	&__link {
		padding: 5px 0; } }



.icon-map, .icon-phone, .icon-mail {
	position: relative;
	top: 2px;
	margin-right: 10px; }
