.main {
	height: 80vh;
	min-height: 600px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	&__input-slider {
		@include f(12px, 500);
		display: flex;
		align-items: center;
		padding: 30px 0;
		margin-bottom: 10vh;
		@include sm {
			display: none; } }
	@include sm {
		height: 100%; } }

.slider {
	max-width: 1100px;
	margin: 0 auto;
	&__item {}
	@include hover {
		.slider__item {
			filter: blur(5px);
			@include sm {
				filter: blur(0); } } }
	& .slider__item {
		@include hover {
			filter: blur(0); } }
	@include sm {
		display: flex;
		flex-wrap: wrap; }

	.slick-list {
		overflow: visible; } }
